.btn {
  width: 15px;
  height: 15px;
  border-radius: 100%;
  border: 1.9px solid #C3EEF8;
  background: white;
  box-shadow: 0px 5px 5px #D8D8D8;
  margin-right: 20px;
  transition: 0.3s ease-out ;
  &:hover {
    background: #C3EEF8;
    transform: scale(1.5);
  }

  &.active{
    background: #6495ED;
  }
}